<template>
  <v-container>
    <div class="main__img">
      <h1>Добро пожалость в компьютерную систему!</h1>
      <img src="../assets/main.jpg" />
    </div>

    <v-row class="divfind" align="center" justify="center">
      <v-btn
        large
        @click="showExploitation()"
        v-show="exploitationButton"
        color="primary"
      >
        Просмотр помещений с учетом ПАК
        <v-icon right>mdi-card-search-outline</v-icon>
      </v-btn>

      <v-btn large @click="showReestr()" v-show="reestr" color="primary">
        Реестр ПАК
        <v-icon right>mdi-card-search-outline</v-icon>
      </v-btn>

      <v-btn large @click="poiskEVMbutton()" v-show="startBtns" color="primary">
        Поиск по названию ПАК
        <v-icon right>mdi-card-search-outline</v-icon>
      </v-btn>
      <v-btn
        v-show="startBtns"
        large
        @click="kriteriyEVMbutton()"
        color="primary"
        >Просмотр ПАК по выбранным критериям</v-btn
      >

      <v-btn
        large
        @click="
          clearBox('exploitation-evm__virtualmodel'),
            clearBox('container'),
            toStart()
        "
        v-show="backToMenu"
        color="primary"
        >Вернуться назад в главное меню</v-btn
      >
    </v-row>

    <!-- поиск по названию -->
    <v-autocomplete
      label="Введите нужное название ПАК"
      :items="evm_api"
      item-text="name"
      return-object
      hide-no-data
      v-model="pickedModel"
      v-show="poisk"
      @change="clearBox('container')"
    ></v-autocomplete>
    <!-- ------- -->

    <!-- просмотр пак -->
    <v-select
      label="Выберите поколение программно-аппаратного комплекса"
      :items="evm_api"
      item-text="generation"
      return-object
      hide-no-data
      v-model="pickedGeneration"
      @change="clearBox('container')"
      v-show="generationClose"
    ></v-select>

    <v-autocomplete
      v-if="pickedGeneration != null"
      label="Выберите тип программно-аппаратного комплекса"
      :items="evm_api"
      item-text="type"
      return-object
      hide-no-data
      v-model="pickedType"
      @change="clearBox('container')"
    ></v-autocomplete>

    <v-autocomplete
      v-if="pickedType != null"
      label="Выберите программно-аппаратный комплекс"
      :items="computedFilterdModels"
      item-text="name"
      return-object
      hide-no-data
      v-model="pickedModel"
      @change="clearBox('container')"
    ></v-autocomplete>
    <!-- ------- -->
    <v-row v-if="pickedModel">
      <v-col cols="6">
        <v-text-field
          outlined
          label="Поколение ПАК"
          :readonly="true"
          :value="pickedModel.generation"
        />
      </v-col>
      <v-col cols="6">
        <v-text-field
          outlined
          label="Тип ПАК"
          :readonly="true"
          :value="pickedModel.type"
        />
      </v-col>
      <v-col cols="6">
        <v-text-field
          outlined
          label="Научные направления и решаемые задачи"
          :readonly="true"
          :value="pickedModel.scientific"
        />
      </v-col>
      <v-col
        cols="6"
        v-for="passports in pickedModel.passport"
        v-bind:key="passports.id"
        ><v-text-field
          outlined
          :label="passports.param.name"
          :readonly="true"
          :value="passports.value"
      /></v-col>
    </v-row>

    <!-- Поиск по периоду эксплуатации  -->
    <div class="exploitation-evm">
      <div class="exploitation-evm__info">
        <v-select
          label="Выберите период эксплуатации"
          :items="exploitation_api"
          item-text="name"
          return-object
          hide-no-data
          v-model="exploitationModel"
          v-show="exploitationPoisk"
        ></v-select>

        <v-select
          v-if="exploitationModel != null"
          :items="sapr"
          label="Выберите подразделение"
          item-text="name"
        ></v-select>

        <v-select
          v-if="exploitationModel != null"
          label="Выберите помещение"
          :items="exploitationModel.exploitation"
          item-text="room.name"
          return-object
          hide-no-data
          v-model="roomModel"
          @change="create3Dexploitation()"
        ></v-select>
        <v-select
          v-if="roomModel != null"
          label="ПАК в данном помещении"
          :items="roomModel.room.rooms3d"
          item-text="evm3d.name"
          return-object
          hide-no-data
          v-model="pickedEVM"
        ></v-select>

        <v-row v-if="pickedEVM != null" v-show="pickedEvmShow">
          <v-col cols="6">
            <v-text-field
              outlined
              label="Поколение ПАК"
              :readonly="true"
              :value="pickedEVM.evm3d.generation"
            />
          </v-col>
          <v-col cols="6">
            <v-text-field
              outlined
              label="Тип ПАК"
              :readonly="true"
              :value="pickedEVM.evm3d.type"
            />
          </v-col>
          <v-col cols="6">
            <v-text-field
              outlined
              label="Научные направления и решаемые задачи"
              :readonly="true"
              :value="pickedEVM.evm3d.scientific"
            />
          </v-col>

          <v-col
            cols="6"
            v-for="passports in pickedEVM.evm3d.passport"
            v-bind:key="passports.id"
            ><v-text-field
              outlined
              :label="passports.param.name"
              :readonly="true"
              :value="passports.value"
          /></v-col>
        </v-row>
        <div class="param-buttons">
          <v-btn
            class="param-buttons__show"
            large
            @click="showParams()"
            v-show="pickedEVM != null"
            color="primary"
            >Показать информацию о ПАК</v-btn
          >
          <v-btn
            large
            @click="closeParams()"
            v-show="pickedEVM != null"
            color="primary"
            >Закрыть информацию о ПАК</v-btn
          >
        </div>
      </div>
      <div
        id="exploitation-evm__virtualmodel"
        v-show="showExploitation3d"
      ></div>
    </div>

    <v-btn @click="create3d()" color="primary" v-if="pickedModel != null"
      >Вывести виртуальную модель</v-btn
    >
    <v-btn
      @click="clearBox('container')"
      color="primary"
      v-if="pickedModel != null"
      >Закрыть виртуальную модель</v-btn
    >

    <div id="container" v-show="show3d"></div>
  </v-container>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Stats from "stats-js";
import axios from "axios";

export default {
  name: "Main",

  data: () => ({
    sapr: {
      name: "САПРиУ",
    },
    pickedEvmShow: false,
    pickedEVM: null,
    roomModel: null,
    exploitationModel: null,
    exploitationPoisk: false,
    pickedType: null,
    pickedGeneration: null,
    pickedModel: null,
    filterdModels: [],
    filterdType: [],
    camera: null,
    point: null,
    hemiLight: null,
    spotLight: null,
    fullScreen: null,
    scene: null,
    controls: null,
    animateID: null,
    renderer: null,
    container: null,
    stats: null,
    loader: null,
    show3d: false,
    showExploitation3d: false,
    showChart: false,
    show3dbutton: false,
    poisk: false,
    poiskEVM: true,
    kriteriyEVM: true,
    generationClose: false,
    startBtns: false,
    backToMenu: false,
    reestr: true,
    exploitationButton: true,
    evm_api: null,
    clear3d: null,
    exploitation_api: null,
    src: "../assets/logo.png",
  }),
  created: function () {
    // axios
    //   .get("http://127.0.0.1:8000/api/evm/")
    //   .then((response) => (this.evm_api = response.data));

    // axios.get("http://127.0.0.1:8000/api/exploitation/").then((response) => {
    //   this.exploitation_api = response.data;
    // });
    axios
      .get("https://test.nutrientplaner.ru:8111/api/evm/")
      .then((response) => (this.evm_api = response.data));

    axios
      .get("https://test.nutrientplaner.ru:8111/api/exploitation/")
      .then((response) => {
        this.exploitation_api = response.data;
      });
  },
  watch: {
    // pickedGeneration: function (generation, oldGeneration) {
    //   if (
    //     generation !== null &&
    //     oldGeneration !== null &&
    //     generation.generation !== oldGeneration.generation
    //   ) {
    //     this.pickedModel = null;
    //   }
    //   if (generation.generation !== null) {
    //     this.filterdModels = this.evm_api.filter((el) => {
    //       return el.generation == this.pickedGeneration.generation;
    //     });
    //     return;
    //   }
    //   this.filterdModels = this.evm_api;
    // },

    pickedType: function (type, oldType) {
      if (type !== null && oldType !== null && type.type !== oldType.type) {
        this.pickedModel = null;
      }
      if (type.type !== null) {
        this.filterdModels = this.evm_api.filter((el) => {
          return (
            el.type == this.pickedType.type &&
            el.generation == this.pickedGeneration.generation
          );
        });
        return;
      }
      this.filterdModels = this.evm_api;
    },
  },

  computed: {
    computedFilterdModels() {
      return this.evm_api.filter((el) => {
        return (
          el.type == this.pickedType.type &&
          el.generation == this.pickedGeneration.generation
        );
      });
    },
  },

  methods: {
    toStart() {
      this.startBtns = false;
      this.poisk = false;
      this.generationClose = false;
      this.poiskEVM = false;
      this.kriteriyEVM = false;
      this.pickedModel = null;
      this.pickedType = null;
      this.pickedGeneration = null;
      this.show3d = false;
      this.reestr = true;
      this.exploitationButton = true;
      this.backToMenu = false;
      this.pickedEVM = null;
      this.roomModel = null;
      this.exploitationModel = null;
      this.exploitationPoisk = false;
      this.showExploitation3d = false;
    },
    poiskEVMbutton() {
      this.poisk = true;
      this.poiskEVM = false;
      this.startBtns = false;
      this.backToMenu = true;
    },
    kriteriyEVMbutton() {
      this.generationClose = true;
      this.kriteriyEVM = false;
      this.startBtns = false;
      this.backToMenu = true;
    },
    showReestr() {
      this.startBtns = true;
      this.exploitationButton = false;
      this.reestr = false;
    },
    showExploitation() {
      this.exploitationButton = false;
      this.exploitationPoisk = true;
      this.reestr = false;
      this.backToMenu = true;
    },
    showParams() {
      this.pickedEvmShow = true;
    },
    closeParams() {
      this.pickedEvmShow = false;
    },

    openFullscreenx2() {
      if (!this.container.fullscreenElement) {
        this.container.requestFullscreen();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        window.addEventListener("resize", function () {
          this.renderer.setSize(window.innerWidth, window.innerHeight);
          (this.camera.aspect = window.innerWidth), window.innerHeight;
        });
      }
      // if (this.container.requestFullscreen) {
      //   this.container.addEventListener("keydown", (e) => {
      //     if (e.key == "F11") setTimeout(() => this.create3d(), 500);
      //   });
      // }
    },
    openFullscreen() {
      if (!this.container.fullscreenElement) {
        this.container.requestFullscreen();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        window.addEventListener("resize", function () {
          this.renderer.setSize(window.innerWidth, window.innerHeight);
          (this.camera.aspect = window.innerWidth), window.innerHeight;
        });
      }
      setTimeout(() => this.openFullscreenx2(), 500);
    },
    closeFullReestr() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
        this.create3d();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
        this.create3d();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
        this.create3d();
      }
    },
    closeFullExploitation() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
        this.create3Dexploitation();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
        this.create3Dexploitation();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
        this.create3Dexploitation();
      }
    },

    create3dReestr() {
      this.container = document.getElementById("container");
      var a = document.createElement("button");
      var b = document.createElement("button");
      a.innerHTML = "Полноэкранный режим";
      a.style.fontSize = "10px";
      a.style.position = "absolute";
      a.style.right = "0";
      a.style.top = "0";
      b.innerHTML = "Закрыть полноэкранный режим";
      b.style.fontSize = "10px";
      b.style.position = "absolute";
      b.style.right = "0";
      b.style.top = "50px";
      this.stats = new Stats();
      this.stats.showPanel(0);
      this.stats.domElement.style.position = "absolute";
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x808080);

      this.renderer = new THREE.WebGLRenderer({ antialias: true });

      this.renderer.setSize(
        this.container.clientWidth,
        this.container.clientHeight
      );
      this.container.innerHTML = "";
      this.container.appendChild(this.renderer.domElement);
      this.container.appendChild(a);
      this.container.appendChild(b);
      // this.container.appendChild(this.stats.dom);

      this.camera = new THREE.PerspectiveCamera(
        60,
        this.container.clientWidth / this.container.clientHeight,
        0.01,
        1000
      );
      this.camera.position.set(5, 5, 10);

      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.listenToKeyEvents(window);
      this.controls.update();

      let abint = new THREE.AmbientLight(0xffffff, 0.5);
      this.hemiLight = new THREE.HemisphereLight(0xffffff, 0x080820, 1);
      this.spotLight = new THREE.SpotLight(0xffffff, 1);
      this.spotLight.position.set(
        this.camera.position.x + 50,
        this.camera.position.y + 50,
        this.camera.position.z + 50
      );
      this.scene.add(abint);
      this.scene.add(this.hemiLight);
      this.scene.add(this.spotLight);

      this.loader = new GLTFLoader();
      this.loader.load(this.pickedModel.evm_model, (gltf) => {
        gltf.scene.scale.set(1.5, 1.5, 1.5, 1.5);
        this.scene.add(gltf.scene);
        this.animate();
      });
      window.addEventListener("resize", function () {
        this.renderer.setSize(
          this.container.clientWidth,
          this.container.clientHeight
        );
        (this.camera.aspect = this.container.clientWidth),
          this.container.clientHeight;
      });
      a.addEventListener("click", this.openFullscreen);
      b.addEventListener("click", this.closeFullReestr);
      // this.container.addEventListener("keydown", (e) => {
      //   if (e.key == "F11") this.create3d() || e.preventDefault();
      // });
      this.this.renderer.dispose();
    },

    create3dExplo() {
      console.log(this.roomModel);
      this.container = document.getElementById(
        "exploitation-evm__virtualmodel"
      );
      var a = document.createElement("button");
      var b = document.createElement("button");
      a.innerHTML = "Полноэкранный режим";
      a.style.fontSize = "10px";
      a.style.position = "absolute";
      a.style.right = "0";
      a.style.top = "0";
      b.innerHTML = "Закрыть полноэкранный режим";
      b.style.fontSize = "10px";
      b.style.position = "absolute";
      b.style.right = "0";
      b.style.top = "50px";
      this.stats = new Stats();
      this.stats.showPanel(1);
      this.container.appendChild(this.stats.dom);

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0xeeeeee);

      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.toneMapping = THREE.ReinhardToneMapping;
      this.renderer.toneMappingExposure = 2.3;
      this.renderer.shadowMap.enabled = true;
      this.renderer.setSize(
        this.container.clientWidth,
        this.container.clientHeight
      );
      this.container.innerHTML = "";
      this.container.appendChild(this.renderer.domElement);
      this.container.appendChild(a);
      this.container.appendChild(b);
      this.camera = new THREE.PerspectiveCamera(
        75,
        this.container.clientWidth / this.container.clientHeight,
        0.01,
        1000
      );
      this.camera.position.set(15, 10, 30);

      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.listenToKeyEvents(window);
      this.controls.update();

      let abint = new THREE.AmbientLight(0xffffff, 0.5);
      this.hemiLight = new THREE.HemisphereLight(0xffffff, 0x080820, 2.5);
      this.spotLight = new THREE.SpotLight(0xffffff, 2);
      this.spotLight.position.set(
        this.camera.position.x + 50,
        this.camera.position.y + 50,
        this.camera.position.z + 50
      );
      this.scene.add(abint);
      this.scene.add(this.hemiLight);
      this.scene.add(this.spotLight);
      this.loader = new GLTFLoader();
      this.loader.load(this.roomModel.room.room_model, (gltf) => {
        gltf.scene.scale.set(1.5, 1.5, 1.5, 1.5);
        this.scene.add(gltf.scene);
        this.animate();
      });
      window.addEventListener("resize", function () {
        this.renderer.setSize(
          this.container.clientWidth,
          this.container.clientHeight
        );
        (this.camera.aspect = this.container.clientWidth),
          this.container.clientHeight;
      });
      this.renderer.dispose();
      a.addEventListener("click", this.openFullscreen);
      b.addEventListener("click", this.closeFullExploitation);
    },

    create3d() {
      this.clearBox("container");
      this.show3d = true;
      setTimeout(() => this.mounted(), 500);
    },
    create3Dexploitation() {
      this.clearBox("exploitation-evm__virtualmodel");
      this.showExploitation3d = true;
      setTimeout(() => this.create3dExplo(), 500);
    },

    animate() {
      this.stats.begin();
      this.stats.update();
      this.animateID = requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
      this.controls.update();
      this.stats.end();
    },

    mounted() {
      this.clearMemory();
      this.create3dReestr();
    },
    clearBox(elementID) {
      cancelAnimationFrame(this.animateID);
      document.getElementById(elementID).innerHTML = "";
      this.show3d = false;
      this.showExploitation3d = false;
    },
    clearMemory() {
      cancelAnimationFrame(this.animateID);
    },
  },
};
</script>

<style lang="scss" scoped>
canvas {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
}

.pososito {
  font-size: 100px;
}

#container {
  position: relative;
  margin: 0 auto;
  margin-top: 20px;
  width: 1100px;
  height: 500px;
  border: 2px solid;
  z-index: 20;
}

#info {
  position: absolute;
  top: 10px;
  width: 100%;
  text-align: center;
  z-index: 100;
  display: block;
}

#exploitation-evm__virtualmodel {
  position: relative;
  margin: 0 auto;
  margin-left: 20px;
  width: 1300px;
  height: 700px;
  border: 1px solid;
  z-index: 20;
}
.exploitation-evm {
  display: flex;
  justify-content: space-between;
}
.exploitation-evm__info {
  width: 500px;
}

.v-btn {
  margin-right: 30px;
}

.param-buttons__show {
  margin-bottom: 30px;
}

.divfind {
  margin-top: 50px;
  margin-bottom: 50px;
}

.main__img {
  text-align: center;
}
</style>